<script>
	import CitrixLogin from "@/components/CitrixLogin.vue";
	import Header from "@/components/Header.vue";
	import Footer from "@/components/Footer.vue";

	export default {
		name: "Login",
		props: ["errorMsg"],
		components: {
			CitrixLogin,
			Header,
			Footer,
		},
		data() {
			return {
				usernameHelp:
					"This can be your username, email, or membership number (but not your surname).",
				error: this.errorMsg || "",
				loading: false,
				userName: "",
				availableEmailUsers: null,
				rememberMe: false,
				redirectUrl: "",
				emailUser: null,
			};
		},
		computed: {
			showEmailUsers() {
				return (
					this.availableEmailUsers &&
					this.availableEmailUsers.length > 1
				);
			},
		},
		methods: {
			populatePage() {
				this.loading = true;
				if (
					typeof this.$route.query != "undefined" &&
					typeof this.$route.query.redirectUrl != "undefined"
				) {
					this.redirectUrl = this.$route.query.redirectUrl;
				}
				this.$ajax
					.post("/auth/PopulatePage", {
						RedirectUrl: this.redirectUrl,
					})
					.then((resp) => {
						if (typeof resp.data != "undefined") {
							if (resp.data.Error) {
								this.error = resp.data.Error;
							} else if (
								resp.data.ClientRedirect &&
								resp.data.RedirectUrl
							) {
								window.location.href = resp.data.RedirectUrl;
							} else if (resp.data.RedirectPage != null) {
								var params = {};
								if (
									resp.data.RedirectPage ==
										"SetupQuestions" &&
									resp.data.Questions.length
								) {
									params = { questions: resp.data.Questions };
								}
								this.$router.push({
									name: resp.data.RedirectPage,
									params: params,
									query: {
										redirectUrl: resp.data.RedirectUrl,
									},
								});
							}

							if (resp.data.RememberMe && resp.data.UserName) {
								this.userName = resp.data.UserName;
								this.rememberMe = true;
							}

							if (
								resp.data.EmailUsers != null &&
								resp.data.EmailUsers.Users != null
							) {
								this.availableEmailUsers =
									resp.data.EmailUsers.Users;
							}
						}
					})
					.catch(() => {})
					.then(() => {
						this.loading = false;
					});
			},
			checkEmail() {
				let emailInput = this.userName;
				if (emailInput && this.$validateEmail(emailInput)) {
					this.loading = true;
					this.$ajax
						.get("/auth/CheckEmail", {
							params: {
								email: emailInput,
							},
						})
						.then((resp) => {
							if (this.userName == resp.data.EmailAddress) {
								this.availableEmailUsers = resp.data.Users;
							}
						})
						.catch()
						.then(() => {
							this.loading = false;
						});
				}
			},
			handleFormSubmit() {
				let data = {
					redirectUrl: this.redirectUrl,
					userName: this.userName,
					password: this.$refs.password.value,
					rememberMe: this.rememberMe,
					emailUser: this.emailUser,
				};
				this.error = "";
				if (data.userName == "" && data.password == "") {
					this.error = "Username and Password cannot be blank.";
				} else if (data.userName == "") {
					this.error = "Username cannot be blank.";
				} else if (data.password == "") {
					this.error = "Password cannot be blank.";
				}
				if (this.error != "") {
					return;
				}

				this.loading = true;

				this.$ajax
					.post("/auth/login", data)
					.then((resp) => {
						if (typeof resp.data != "undefined") {
							if (resp.data.Error != null) {
								this.error = resp.data.Error;
							} else if (
								resp.data.ClientRedirect &&
								resp.data.RedirectUrl.length
							) {
								window.location.href = resp.data.RedirectUrl;
							} else if (resp.data.RedirectPage.length) {
								var params = {};
								if (
									resp.data.RedirectPage ==
										"SetupQuestions" &&
									resp.data.Questions.length
								) {
									params = { questions: resp.data.Questions };
								}
								this.$router.push({
									name: resp.data.RedirectPage,
									params: params,
									query: {
										redirectUrl: resp.data.RedirectUrl,
									},
								});
							}
						}
					})
					.catch(() => {})
					.then(() => {
						this.loading = false;
					});
			},
		},
		mounted() {
			this.populatePage();
		},
	};
</script>

<template>
	<div>
		<div class="login-underlay" id="login-underlay">
			<Header />
			<Footer />
		</div>
		<div class="login">
			<div class="login-box-bg hidden" id="login-box-bg"></div>

			<div class="login-box-wrapper">
				<div class="login-box smaller" id="login-box">
					<div class="box-title">
						<span class="icon-key"></span> Login
					</div>
					<div class="box-body">
						<div class="errormsg" v-html="this.error"></div>
						<form @submit.prevent="handleFormSubmit">
							<fieldset>
								<div class="errormsg hidden">
									We could not log you in
								</div>
								<div class="login-box-row label">
									<input
										type="text"
										id="username"
										name="username"
										v-model="userName"
										autocomplete="off"
										placeholder="Username or email"
										v-on:change="checkEmail"
									/>
									<div
										class="username-question"
										v-tooltip="usernameHelp"
									>
										<span class="icon-help-round"></span>
									</div>
								</div>
								<div
									class="login-box-row label"
									v-if="showEmailUsers"
									v-cloak
								>
									<select
										id="emailuser"
										v-model="emailUser"
										name="emailuser"
									>
										<option value="null" disabled selected>
											Username and company
										</option>
										<option
											v-for="user in availableEmailUsers"
											v-bind:value="user.UserName"
											:key="user.UserName"
										>
											{{ user.UserName }},
											{{ user.CompanyName }} ({{
												user.CompanyNumber
											}})
										</option>
									</select>
								</div>
								<div class="login-box-row label">
									<input
										type="password"
										id="password"
										name="password"
										ref="password"
										autocomplete="off"
										placeholder="Password"
									/>
								</div>
								<div class="login-box-row buttons">
									<div class="loader" v-show="loading"></div>
									<input
										type="submit"
										value="Login"
										class="button green"
										id="loginButton"
										v-show="!loading"
										v-cloak
									/>
								</div>
								<div class="login-box-row buttons">
									<input
										type="checkbox"
										id="rememberMe"
										name="rememberMe"
										v-model="rememberMe"
									/>
									<label for="rememberMe"
										>Remember my username</label
									>
								</div>
								<div class="login-box-row buttons stacked">
									<router-link
										:to="{
											path: '/Recover',
											query: { redirectUrl: redirectUrl },
										}"
										>I have forgotten my
										password</router-link
									>
								</div>
							</fieldset>
						</form>
					</div>
				</div>
			</div>
			<CitrixLogin />
		</div>
	</div>
</template>
